.user-trips {
  order: 4;

  background-color: #f2f2f2;
}

.user-trips__container {
  width: 320px;
  margin: 0 auto;
  padding: 38px 20px 0 20px;
}

.user-trips__fieldset {
  margin: 0;
  padding: 0;

  border: none;
}

.user-trips__legend {
  width: 222px;
  margin: 0 auto 42px auto;

  font-size: 14px;
  line-height: 18px;
  color: @pink-color;
  text-align: center;
  text-transform: uppercase;
}

.user-trips__list {
  .ls-reset();
}

.user-trips__item {
  margin-bottom: 42px;
}

.user-trips__label {
  position: relative;

  display: block;
  padding: 5px 10px 5px 70px;

  font-size: 18px;
  line-height: 30px;
  color: @base-color;

  cursor: pointer;
}

.user-trips__label:hover .user-trips__checkbox-indicator {
  border-color: @base-color;
}

.user-trips__label:active .user-trips__checkbox-indicator {
  border-color: @pink-color;
}

.user-trips__checkbox-indicator {
  position: absolute;
  left: 0;
  top: 0;

  width: 40px;
  height: 40px;

  background-color: #ffffff;
  border: solid 2px #e5e5e5;
}

.user-trips__input:focus + .user-trips__checkbox-indicator {
  outline: thin dotted;
}

.user-trips__input:checked + .user-trips__checkbox-indicator::before {
  content: "";

  position: absolute;
  left: 9px;
  top: 9px;

  width: 35px;
  height: 35px;

  background: url("../img/icon-check.svg") no-repeat;
}

@media (min-width: @tablet-width) {
  .user-trips {
    background-color: #ffffff;
  }

  .user-trips__container {
    width: 660px;
    padding: 64px 20px 67px 20px;
  }

  .user-trips__fieldset {
    position: relative;

    padding-top: 52px;
    padding-bottom: 7px;

    border: 2px solid #e5e5e5;
  }

  .user-trips__legend {
    position: absolute;
    top: -15px;
    left: 88px;

    width: 438px;

    line-height: 30px;

    background-color: #ffffff;
  }

  .user-trips__label {
    padding-left: 117px;
  }

  .user-trips__checkbox-indicator {
    left: 58px;
  }
}

@media (min-width: @desktop-width) {
  .user-trips__container {
    width: 940px;
    padding: 66px 0 0 0;
  }

  .user-trips__fieldset {
    padding-top: 51px;
    padding-bottom: 14px;
    padding-left: 58px;
  }

  .user-trips__legend {
    left: 248px;
  }

  .user-trips__list {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    margin-left: -47px;
  }

  .user-trips__item {
    width: 260px;
    margin-left: 50px;
    margin-bottom: 42px;
  }

  .user-trips__item:nth-child(1) {
    order: 1;
  }

  .user-trips__item:nth-child(2) {
    order: 4;
  }

  .user-trips__item:nth-child(3) {
    order: 2;

    width: 245px;
  }

  .user-trips__item:nth-child(4) {
    order: 5;

    width: 245px;
  }

  .user-trips__item:nth-child(5) {
    order: 3;
  }

  .user-trips__item:nth-child(6) {
    order: 6;
  }

  .user-trips__label {
    padding-left: 59px;
    padding-right: 0;
  }

  .user-trips__checkbox-indicator {
    left: 0;
  }
}
